import { SVGProps } from "react";

// ==============================
// Frontend Types
// ==============================

export type IconSvgProps = SVGProps<SVGSVGElement> & {
  size?: number;
};

// ==============================
// Core Types
// ==============================

// Transaction
export enum TransactionTagType {
  Buy = "Buy",
  Sell = "Sell",
  Transfer = "Transfer",
  Deposit = "Deposit",
  Withdraw = "Withdraw",
  Swap = "Swap",
  Other = "Other",
}
export interface Transaction {
  id: string;
  time: Date;
  from: Profiler;
  to: Profiler;
  value: number | string;
  token: Token;
  tags: TransactionTagType[];
}

// Project
export interface Project {
  address: string;
  name: string;
  description: string;

  [key: string]: any;
}

// Profiler
export enum ProfilerTagType {
  Whale = "Whale",
  Fund = "Fund",
  Anonymous = "Anonymous"
}
export interface Profiler {
  name?: string;
  address: string;
  avatar?: string;
  tag?: ProfilerTagType
}


// Token
export interface Token {
  name: string;
  symbol?: string;
  address: string;
  icon?: string;
}


// Key Metric
export interface MetricCategory {
  id: string | number;
  name: string;
  metrics: KeyMetric[];
}
export interface KeyMetric {
  id: string | number;
  name: string;
  value: number | string;
  unit: string;
}